<template>
	<div class="container">
		<h1 class="header not-found--header">Сторінку не знайдено <span class="not-found--icon"></span></h1>
	</div>
</template>

<script>
	export default {
        name: "NotFound"
    }
</script>

<style>
	.not-found--icon {
		display: inline-block;
		width: 32px;
		height: 32px;
		background: url(/Content/img/crying.svg) center/contain no-repeat;
	}

	@media only screen and (max-width: 1195px) {
		.not-found--icon {
			width: 27px;
			height: 27px;
		}
	}

	@media only screen and (max-width: 768px) {
		.not-found--icon {
			width: 25px;
			height: 25px;
		}
	}
</style>



